.about__card {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about__icon {
  /* margin-right: 0.5rem; */
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.about__card h3 {
  font-size: 1rem;
}

p {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 0.5rem;
  text-indent: 1rem;
}

.bullets {
  display: grid;
  justify-content: center;
  margin: 1rem;
}

.li__child {
  font-style: italic;
  margin-left: 1.5rem;
  list-style-type: "тед ";
}

.about_btn {
  font-size: 1rem;
  margin-right: 1rem;
  color: rgb(254, 254, 254);
  background-color: rgba(115, 138, 0, 0.358);
  transition-duration: 0.4s;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 1rem;
  border: transparent;
  margin-bottom: 1rem;
}

.about_btn:hover {
  color: rgb(255, 255, 255);
  background-color: rgba(0, 213, 25, 0.468);
  border-color: rgba(252, 252, 252, 0.297);
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

/* ============small device=============== */

@media screen and (max-width: 800px) {
  .about__container {
    display: grid;
    justify-content: center;
    align-items: center;
    /* gap: 2rem; */
  }
}

